function renderPixelCard(variant: string, imgSrc: string, text: any, gap: any, speed: any, colors: any, noFocus: any) {
  return (
    <PixelCard 
      variant={variant} 
      text={text}
      gap={gap}
      speed={speed}
      colors={colors}
      noFocus={noFocus}
    >
      <img src={imgSrc} className="w-full h-auto" style={{ maxHeight: '300px', maxWidth: "300px" }}/>
    </PixelCard>
  );
}

